import PropTypes from 'prop-types';
import './Input.scss';

/**
 *  .
 *     .
 */
export const Input = ({
  inputId,
  variant,
  name,
  onChange,
  value,
  placeholder,
  type,
  minLength,
  maxLength,
  isValid,
  pattern,
}) => {
  return (
    <input
      name={name}
      id={inputId}
      onChange={(e) => onChange(e)}
      value={value}
      className={`input input_kind_${variant} ${
        isValid ? '' : 'input_invalid'
      }`}
      placeholder={placeholder}
      type={type}
      minLength={minLength}
      maxLength={maxLength}
      pattern={pattern}
    />
  );
};

Input.propTypes = {
  /**
   *    
   */
  variant: PropTypes.oneOf([
    'form',
    'password',
    'info',
    'search',
    'price',
    'other',
  ]),
  /**
   * ,     
   */
  // onChange: PropTypes.func,
  /**
   * Id ,     
   */
  inputId: PropTypes.string,
  /**
   *  
   */
  name: PropTypes.string,
  /**
   *  
   */
  value: PropTypes.string,
  /**
   *  
   */
  placeholder: PropTypes.string,
  /**
   *    
   */
  type: PropTypes.string,
};

Input.defaultProps = {
  variant: 'form',
  onChange: undefined,
  inputId: undefined,
  name: undefined,
  value: undefined,
  placeholder: undefined,
  type: undefined,
  children: undefined,
  required: true,
  minLength: '2',
  maxLength: '30',
};
