import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';

import { useClickOutside } from '../../../../hooks/useClickOutside';
import './SelectOption.scss';
import { findLineByStation } from '../../../../utils/filtersUtils';

export const SelectOption = React.memo(({ onChange, options }) => {
  const { underground } = useSelector((state) => state.filter); //     Redux

  const [inputValue, setInputValue] = useState(''); //    
  const [searchOption, setSearchOption] = useState(options); //    
  const [isOpen, setIsOpen] = useState(false); //   /  

  const containerRef = useRef(null); //          

  // ,         
  useClickOutside(containerRef, () => {
    if (isOpen) setTimeout(() => setIsOpen(false), 200); //     
  });

  //         
  useEffect(() => {
    searchOptions();
  }, [inputValue, options]);

  //       
  function searchOptions() {
    const searchOpt = options.filter((option) => {
      return option.name.toLowerCase().includes(inputValue.toLowerCase()); //        ()
    });
    setSearchOption(searchOpt); //    
  }

  //  ,     
  function isOptionSelected(optionName) {
    return underground.some((option) => option.name === optionName);
  }

  //       
  return (
    <div
      ref={containerRef}
      onClick={(e) => {
        e.stopPropagation(); //   
        setIsOpen((prev) => !prev); // /   
      }}
      className='select'
    >
      <div className='wrapper-value'>
        <span className='value'>
          {/*         */}
          {underground.map((selectedOption, index) => (
            <button
              key={index}
              onClick={(e) => {
                e.stopPropagation(); //   
                onChange(selectedOption); //    
              }}
              className='option-badge'
            >
              {selectedOption.name} {/*    */}
              <span
                className='metro-color'
                style={{
                  backgroundColor: `#${findLineByStation(selectedOption.name)}`,
                }}
              ></span>
              <span className='remove-btn'>&times;</span>{' '}
              {/*    */}
            </button>
          ))}
          <input
            placeholder=' ' //    
            className='search-value' //   
            value={inputValue} //    
            onChange={(e) => setInputValue(e.target.value)} //     
          />
        </span>
      </div>
      <ul className={`options ${isOpen ? 'show' : ''}`}>
        {/*    */}
        {searchOption.map((option) => (
          <li
            onClick={(e) => {
              e.stopPropagation(); //   
              onChange(option); //    
              setIsOpen(false); //  
              setInputValue(''); //   
            }}
            key={option.slug}
            className={`option${
              isOptionSelected(option.name) ? ' selected' : ''
            }`}
          >
            {option.name} {/*    */}
            <span
              className='metro-color'
              style={{ backgroundColor: `#${findLineByStation(option.name)}` }}
            ></span>
          </li>
        ))}
      </ul>
    </div>
  );
});

//    props 
SelectOption.propTypes = {
  value: PropTypes.array, //  
  onChange: PropTypes.func, //   
  options: PropTypes.array, //  
};

//     props 
SelectOption.defaultProps = {
  value: [''], //      
  onChange: undefined, //     
  options: [], //     
};
